body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: #545454;
  background-color: #fafafa;
  font-family: "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.6;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}
.btn {
  display: block;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s linear;
  text-align: center;
  color: #292929;
  border: 2px solid #f1cdb3;
  border-radius: 30px;
  background-color: #fafafa;
  font-family: "Georgia", sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 1;
  outline: none;
}
.btn:hover,
.btn:focus {
  background-color: #f1cdb3;
}
.btn:active {
  opacity: 0.5;
}
.header {
  background: #f6f6f6;
}
.header--main {
  background: #2b2625 url("../img/header_background.jpg") center 0;
}
.header__wrap {
  box-sizing: border-box;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}
.header__logo-link {
  padding: 50px 0;
}
.header__logo-link:hover,
.header__logo-link:focus {
  opacity: 0.8;
}
.header__logo-link:active {
  opacity: 0.5;
}
.main-nav--main .main-nav__link {
  color: #cdcdcd;
}
.main-nav__toggle {
  position: absolute;
  display: none;
}
.main-nav__link {
  position: relative;
  color: #545454;
  margin-left: 40px;
  outline: none;
}
.main-nav__link:hover::before,
.main-nav__link:focus::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 3px;
  background-color: #f1cdb3;
  bottom: -8px;
  border-radius: 2px;
}
.main-nav__link:active {
  opacity: 0.5;
}
.main-nav__link--active::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 3px;
  background-color: #f1cdb3;
  bottom: -8px;
  border-radius: 2px;
}
.sub-header__wrap {
  box-sizing: border-box;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px 435px;
}
.sub-header__title {
  max-width: 370px;
  margin: 0;
  margin-bottom: 45px;
  color: #fafafa;
  font-family: "Georgia", sans-serif;
  font-size: 44px;
  font-weight: 400;
  line-height: 57px;
}
.sub-header__text {
  max-width: 470px;
  margin-bottom: 45px;
  color: #cdcdcd;
}
.sub-header__btn {
  width: 190px;
  padding: 18px 0;
  border-radius: 36px;
}
.about {
  box-sizing: border-box;
  padding: 80px 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
}
.about__img {
  margin-right: 100px;
}
.about__title {
  margin: 0 0 35px;
  max-width: 315px;
  font-size: 35px;
  line-height: 45px;
  font-family: "Georgia", sans-serif;
  font-weight: 400;
}
.about__text {
  max-width: 420px;
}
.cards {
  padding-bottom: 60px;
  background-color: #f6f6f6;
}
.cards__title {
  margin: 0 auto;
  padding: 70px 0;
  text-align: center;
  font-family: "Georgia", sans-serif;
  font-size: 35px;
  line-height: 45px;
}
.cards__slider {
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
.cards__btn {
  width: 260px;
  margin: 0 auto;
  padding: 18px 0;
}
.cards__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
.cards__item {
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  width: 270px;
  margin: 0 10px 30px;
  border-radius: 8px;
  background-color: #fafafa;
}
.cards__item:hover,
.cards__item:focus-within {
  background-color: #ffffff;
}
.cards__img {
  width: 100%;
  height: 270px;
}
.cards__item-title {
  margin: 0;
  margin-top: 30px;
  text-align: center;
  font-family: "Georgia", sans-serif;
  font-size: 20px;
  font-weight: 400;
}
.cards__item-btn {
  margin: 20px 0 32px;
  padding: 18px 45px;
}
.cards__pagination {
  width: 280px;
  margin: 0 auto;
}
.pagination {
  display: flex;
  justify-content: space-between;
  padding: 0;
}
.pagination__item {
  list-style: none;
  display: flex;
  align-items: center;
}
.pagination__link {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  color: #292929;
  border: 2px solid #f1cdb3;
  border-radius: 50%;
  font-size: 20px;
}
.pagination__link--active {
  background-color: #f1cdb3;
}
.pagination__link:hover {
  background-color: #f1cdb3;
}
.pagination__three-dots {
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  background: url("../img/three-dots.png") center center no-repeat;
}
.slider {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 60px;
}
.slider__btn {
  box-sizing: border-box;
  top: 50%;
  width: 50px;
  height: 50px;
  position: absolute;
  display: block;
  border: 1px solid #f1cdb3;
  background: url("../img/long-left-arrow.svg") center center no-repeat;
  border-radius: 50%;
  transform: translateY(-50%);
}
.slider__btn--prev {
  left: 0;
}
.slider__btn--next {
  right: 0;
  transform: rotate(180deg);
}
.slider__btn:hover,
.slider__btn:focus {
  background-color: #f1cdb3;
}
.slider__btn:active {
  opacity: 0.5;
}
.help__title {
  text-align: center;
  margin: 68px 0;
  font-size: 35px;
  line-height: 45px;
  font-family: "Georgia", sans-serif;
  font-weight: 400;
}
.help__wrap {
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.help__link {
  min-width: 155px;
  width: 20%;
  margin-bottom: 70px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.help__link:hover .help__text,
.help__link:focus .help__text {
  color: #000000;
}
.help__link:hover .help__link-img,
.help__link:focus .help__link-img {
  fill: #d97832;
}
.help__link:active .help__text {
  color: #6e6e6e;
}
.help__link:active .help__link-img {
  fill: #f9e9de;
}
.help__link-img {
  margin-bottom: 25px;
  fill: #f1cdb3;
}
.help__text {
  margin: 0;
  font-size: 20px;
  font-family: "Georgia", sans-serif;
  font-weight: 400;
  color: #545454;
}
.donation {
  box-sizing: border-box;
  padding: 80px 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #f6f6f6;
}
.donation__img {
  margin-right: 60px;
  width: 505px;
  height: 261px;
}
.donation__title {
  margin: 0 0 30px;
  max-width: 315px;
  font-size: 35px;
  line-height: 45px;
  font-family: "Georgia", sans-serif;
  font-weight: 400;
}
.donation__subtitle {
  font-size: 15px;
  font-family: "Georgia", sans-serif;
  margin-bottom: 15px;
}
.donation__card {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 320px;
  border-radius: 8px;
  background-color: #f1cdb3;
}
.donation__card-text {
  font-size: 20px;
  padding: 14px 0;
}
.donation__text {
  max-width: 355px;
  font-size: 12px;
  font-style: italic;
}
.footer {
  padding: 70px 0 50px;
  background: #2b2625 url("../img/footer_background.jpg") center 0;
}
.footer__wrap {
  box-sizing: border-box;
  padding: 0 15px;
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
}
.footer__question {
  width: 300px;
  margin-right: 150px;
}
.footer__address {
  width: 300px;
}
.footer__title {
  color: #ffffff;
  font-size: 35px;
  line-height: 45px;
  margin: 0;
  font-family: "Georgia", sans-serif;
}
.footer__list {
  padding: 0;
}
.footer__item {
  height: 60px;
  list-style: none;
  display: flex;
  align-items: center;
}
.footer__link {
  width: 100%;
  display: flex;
  align-items: center;
  color: #f1cdb3;
}
.footer__link:hover,
.footer__link:focus {
  color: #fafafa;
}
.footer__link:hover .footer__link-img,
.footer__link:focus .footer__link-img {
  fill: #fafafa;
}
.footer__link:active {
  color: rgba(250, 250, 250, 0.3);
}
.footer__link:active .footer__link-img {
  fill: rgba(250, 250, 250, 0.3);
}
.footer__link-img {
  fill: #f1cdb3;
  margin-right: 20px;
}
